@import url(https://fonts.googleapis.com/css2?family=Zen+Antique+Soft&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zen+Antique+Soft&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Serif+Pro:wght@600&family=Courgette&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, rgb(19, 38, 77),rgb(44, 33, 17));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.wrapper{
    height: 100vh;
    height: 60rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.switchBtn{
    font-size: 2rem;
    padding: 1rem 2.5rem;
    border-radius: 2rem;
    border: none;
    margin: 1rem auto;
    background-color: #001;
    color: #ffffff;
    cursor: pointer;
    transition: all 350ms ease-in;
}
.switchBtn:hover,.switchBtn:focus-visible{
    box-shadow: 0 0 3px 2px currentColor;
    text-shadow: 0 0 2px currentColor;
    outline: none;
}



.button-wrapper{
    font-size: 2rem;
    padding: 1em 1em;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border: 3px solid rgb(75, 7, 153);
    border-radius: 5%;
    font-family: 'Zen Antique Soft', serif;
}
.button-wrapper:hover{
    border: 3px solid currentColor;
    text-shadow: currentColor 1px 0 4px;
}


.calc-wrapper{
    height: 45rem;
    width: 29rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.buttons{
    height: 35rem;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4,7rem);
    grid-template-rows: repeat(6,5.7rem);
    grid-gap: 0.35rem;
    gap: 0.35rem;
    padding: 0.25rem 0;
}
.calculator{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 90rem;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}
.themeBtn{
    font-size: 1.25rem;
    padding: 1em 2em;
    background: #000;
    color: #fff;
    border: none;
    box-shadow: 0 0 4px 0 rgb(230, 4, 230);
    cursor: pointer;
    transition: all 500ms ease-in;
}
.themeBtn:hover,.themeBtn:focus-visible{
    box-shadow: 0 0 10px 0 rgb(230, 4, 230);
    outline: none;
}
@media(max-width:500px){
    html{
        font-size: 62.5%;
    }
}
@media(max-height:800px){
    html{
        font-size: 62.5%;
    }
}
@media(max-height:520px){
    html{
        font-size: 43.5%;
    }
}
@media(max-width:300px){
    html{
        font-size: 43.5%;
    }
}
.output-wrapper{
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    flex-direction: column;
    justify-content: space-evenly;
    width: 100%;
    height: 9rem;
    font-family: 'Zen Antique Soft', serif;
    text-align: end;
}
.result{
    font-size: 2rem;
    text-shadow: currentColor 0px 0 2px;
}
.userInput{
    font-size: 1.8rem;
    overflow-wrap: break-word;
    overflow-y: scroll;
    width: 100%;
    max-width: 100%;
    scroll-behavior: smooth;
}
.userInput::-webkit-scrollbar{
    width: 2px;
}
.userInput::-webkit-scrollbar-thumb{
    background-color: rgb(255, 255, 255);
}
.userInput::-webkit-scrollbar-track{
    background-color: #000;
}


*::-webkit-scrollbar{
    scroll-behavior: smooth;
    width: 3px;
}
*::-webkit-scrollbar-thumb{
    background-color: #5f5e5e;
    border-radius: 20%;
}
*::-webkit-scrollbar-track{
    background-color: #fff;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0;
}
.convertor{
    display: flex;
    justify-content: space-evenly;
    background-color: hsl(200,50%,65%);
    width: 60rem;
    min-height: 25rem;
    max-height: auto;
    padding: 1.5rem;
    border-radius: 1rem;
    grid-gap: 1rem 2rem;
    gap: 1rem 2rem;
    flex-wrap: wrap;
    font-family: 'Source Serif Pro', sans-serif;
}
.switchConvertor{
    margin-bottom: 0.25rem;
}
.switchConvertor > span{
    color: #fff;
    font-size: 2rem;
    font-family: 'Source Serif Pro';
    margin-inline: 1rem;
}
.switchConvertor > select{
    border: none;
    font-size: 1.75rem;
    font-family: 'Source Serif Pro'; 
}
.switchConvertor > select{
    outline: none;
    border: none;
}
.select{
    display: flex;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}
.select > input{
    width: 18rem;
    font-size: 1.75rem;
    font-family: 'Source Serif Pro'; 
    padding: 0.2rem 0.25rem;
    font-weight: 600;
    border: none;
    border-radius: 0.25rem;
}
.select > input:focus-visible{
    outline: none;
    border: 2px solid #e9bd38;
    background-color: #ffa;
}
div > h2{
    font-size: 2.5rem;
}
.userInputDiv,.outputDiv{
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
}
.convertedValue{
    background-color: #fff;
    width: 20rem;
    height: 2.75rem;
    padding: 0.25rem;
    font-size: 1.625rem;
    font-family: 'Source Serif Pro', sans-serif;
    font-weight: 700;
}
.reset{
    margin-top: 2.75rem;
    font-family: 'Source Serif Pro', sans-serif;
    font-size: 1.75rem;
    font-weight: 600;
    padding: 0.375em 1.625em;
    border: none;
    border-radius: 1em;
    background-color: #ffe;
    color: #000;
    cursor: pointer;
    transition: all 250ms ease-in;
}
.reset:focus-visible,.reset:hover{
    background-color: rgb(46, 46, 46);
    color: #e7e7e7;
    outline: none;
}
.answerDiv{
    width: 75%;
    display: flex;
    flex-wrap: wrap;  
    justify-content: space-evenly;
    align-items: flex-start;
}
.go{
    font-size: 1.75rem;
    font-family: 'Source Serif Pro', sans-serif;
    font-weight: 600;
    padding: 0.125em 0.75em;
    max-width: 6rem;
    border: none;
    outline: none;
    border-radius: 0.25rem;
    cursor: pointer;
    background-color: #fff;
    color: #000;
    transition: all 250ms ease-in;
}
.go:hover,.go:focus-visible{
    color: #e7e7e7;
    background-color: #1d1d1d;
}
.custom-select-measurement{
    font-family: 'Source Serif Pro', sans-serif;
    font-size: 1.5rem;
}
.custom-select-unit{
    font-family: 'Courgette', cursive;
    font-size: 1.125rem;
}
@media(max-width:980px){
    .convertor{
        width: 80%;
        grid-gap: 0.5rem 2rem;
        gap: 0.5rem 2rem;
    }
}
@media(min-width:280px) and (max-width:700px){
    .select {
        flex-direction: column;
    }
    .outputDiv .select{
        align-items: center;
    }
}
@media(min-width:1200px){
    .convertor{
        width: 70rem;
    }
}
